.active {
  font-size: 14px;
  color: #007AFF;
  font-weight: bold;
}
.head {
  width: 100%;
  line-height: 40px;
  font-size: 14px;
  color: #666;
  background: #FFF;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
}
.head header {
  width: 1160px;
  height: 40px;
  margin: 0 auto;
}
.head header ul li {
  cursor: pointer;
  float: left;
  margin-left: 32px;
}
.head header ul li:nth-child(1) {
  margin-left: 20px;
}
@media screen and (min-width: 1640px) {
  .head {
    width: 100%;
    line-height: 40px;
    font-size: 14px;
    color: #666;
    background: #FFF;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  }
  .head header {
    width: 1640px;
    height: 40px;
    margin: 0 auto;
  }
  .head header ul li {
    cursor: pointer;
    float: left;
    margin-left: 32px;
  }
  .head header ul li:nth-child(1) {
    margin-left: 20px;
  }
}
