@media screen and (max-width: 1640px) {
  .state-cont .line .dot {
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background: #007AFF;
    margin-left: 38px;
    margin-bottom: 4px;
    margin-top: 2px;
  }
  .state-cont .line .dot span {
    position: absolute;
    left: 0;
    top: -6px;
  }
  .state-cont .line .line_long {
    float: left;
    width: 1px;
    height: 150px;
    background: #007AFF;
    margin-left: 39px;
  }
  .state-cont .contDetail {
    position: absolute;
    top: -4px;
    margin-left: 64px;
    width: 696px;
    height: 140px;
    background: url(data:image/png;base64,);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-bottom: 16px;
    margin-top: -14px;
  }
  .state-cont .contDetail .cont-text {
    float: left;
    padding-left: 27px;
    padding-top: 17px;
  }
  .state-cont .contDetail .cont-text p:first-child {
    font-size: 12px;
    color: #666666;
    margin-bottom: 20px;
  }
  .state-cont .contDetail .cont-text p:nth-child(2) {
    font-size: 16px;
    color: #343434;
    font-weight: bold;
    width: 472px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
  }
  .state-cont .contDetail .contImg {
    float: right;
    width: 140px;
    height: 80px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-top: 30px;
    margin-right: 30px;
  }
}
