@media screen and (max-width: 1640px) {
  .recent-state {
    width: 760px;
    height: 800px;
  }
  .recent-state .state-tit {
    font-weight: bold;
    font-size: 18px;
    color: #333333;
    height: 45px;
    line-height: 45px;
    border-bottom: 1px solid #D8D8D8;
  }
  .recent-state .stateList {
    padding-top: 10px;
  }
  .recent-state .stateList .list-cont .year span {
    float: left;
  }
  .recent-state .stateList .list-cont .year img {
    margin-left: 33px;
  }
  .recent-state .stateList .list-cont .line_short {
    width: 1px;
    height: 10px;
    background: #007AFF;
    margin-left: 39px;
    margin-bottom: 4px;
  }
  .recent-state .stateList .list-cont .stateItem {
    position: relative;
    padding-bottom: 16px;
    height: 162px;
  }
  .recent-state .stateList .list-cont .last-dot {
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background: #007AFF;
    margin-left: 38px;
    margin-bottom: 4px;
    margin-top: 2px;
  }
}
