.activeBlue {
  color: #007AFF;
  background: #63A5E8;
}
.navBlue {
  width: 100%;
  background: #007AFF;
}
.navBlue nav {
  width: 1160px;
  height: 48px;
  margin: 0 auto;
}
.navBlue nav .footballLogo {
  width: 100px;
  padding: 12px 0 15px 0;
  font-size: 16px;
  color: #fff;
  float: left;
}
.navBlue nav .footballLogo .football {
  width: 20px;
  height: 20px;
  background-image: url(data:image/png;base64,);
  background-size: 100% 100%;
  float: left;
  line-height: 20px;
  vertical-align: middle;
}
.navBlue nav .footballLogo .footballData {
  width: 79px;
  height: 20px;
  border-right: 1px solid #fff;
  float: left;
  font-size: 15px;
  font-weight: bold;
  line-height: 20px;
}
.navBlue nav ul {
  height: 48px;
  float: left;
}
.navBlue nav ul li {
  float: left;
  height: 48px;
  padding: 0 20px;
  line-height: 48px;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
}
@media screen and (min-width: 1640px) {
  .navBlue {
    width: 100%;
    background: #007AFF;
  }
  .navBlue nav {
    width: 1640px;
    height: 48px;
    margin: 0 auto;
  }
  .navBlue nav .footballLogo {
    width: 100px;
    padding: 12px 0 15px 0;
    font-size: 16px;
    color: #fff;
