    font-weight: bold;
    line-height: 20px;
  }
  .navBlue nav ul {
    height: 48px;
    float: left;
  }
  .navBlue nav ul li {
    float: left;
    height: 48px;
    padding: 0 20px;
    line-height: 48px;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
  }
}
