@media screen and (max-width: 1640px) {
  .playerInfo .playerInfo-tit {
    font-weight: bold;
    font-size: 14px;
    color: #333333;
    height: 45px;
    line-height: 45px;
  }
  .playerInfo .list-cont .p-img {
    width: 45px;
    height: 45px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    float: left;
  }
  .playerInfo .list-cont .p-desc {
    margin-left: 45px;
    padding-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .playerInfo .list-cont .p-desc .name {
    font-size: 14px;
    color: #5F5F5F;
    font-weight: bold;
    line-height: 25px;
  }
  .playerInfo .list-cont .p-desc .position {
    font-size: 12px;
    color: #B2B2B2;
  }
  .playerInfo .list-cont ul > li {
    float: left;
    width: 170px;
    height: 60px;
    padding: 6px 0 6px 10px;
    box-sizing: border-box;
  }
  .playerInfo .coachName {
    padding: 6px 0 6px 10px;
    height: 60px;
  }
}
@media screen and (min-width: 1640px) {
  .playerInfo .playerInfo-tit {
    font-weight: bold;
    font-size: 18px;
    color: #333333;
    height: 45px;
    line-height: 45px;
  }
  .playerInfo .list-cont .p-img {
    width: 60px;
    height: 60px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    float: left;
  }
  .playerInfo .list-cont .p-desc {
    margin-left: 45px;
    padding-left: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .playerInfo .list-cont .p-desc .name {
    font-size: 14px;
    color: #5F5F5F;
    font-weight: bold;
    line-height: 25px;
  }
  .playerInfo .list-cont .p-desc .position {
    font-size: 12px;
    color: #B2B2B2;
  }
  .playerInfo .list-cont ul > li {
    float: left;
    width: 250px;
    height: 80px;
    padding: 6px 0 6px 10px;
    box-sizing: border-box;
  }
  .playerInfo .coachName {
    padding: 6px 0 6px 10px;
    height: 80px;
  }
}
