@media screen and (max-width: 1640px) {
  .wrapper-teamInfo {
    margin-bottom: 20px;
  }
  .wrapper-teamInfo .teamImg {
    margin-right: 40px;
    float: left;
    padding: 45px 0 45px 42px;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  }
  .wrapper-teamInfo .teamImg .teamLogo {
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    float: left;
  }
  .wrapper-teamInfo .teamImg .name {
    margin-left: 96px;
    width: 302px;
    padding-left: 22px;
    box-sizing: border-box;
  }
  .wrapper-teamInfo .teamImg .name .cname {
    font-size: 18px;
    font-weight: bold;
    color: #333333;
    margin-bottom: 10px;
    margin-top: 15px;
  }
  .wrapper-teamInfo .teamImg .name .enName {
    font-size: 18px;
    color: #B3B3B3;
  }
  .wrapper-teamInfo .teamInfo {
    width: 280px;
    height: 180px;
    float: left;
  }
  .wrapper-teamInfo .teamInfo .info-tit {
    font-weight: bold;
    font-size: 18px;
    color: #333333;
    border-bottom: 1px solid #D8D8D8;
    line-height: 42px;
  }
  .wrapper-teamInfo .teamInfo .info-desc {
    font-size: 12px;
    color: #676767;
    padding-top: 10px;
  }
  .wrapper-teamInfo .teamInfo .info-desc li {
    margin-bottom: 10px;
  }
}
@media screen and (min-width: 1640px) {
  .wrapper-teamInfo {
    margin-bottom: 20px;
  }
  .wrapper-teamInfo .teamImg {
    margin-right: 50px;
    float: left;
    padding: 58px 0 58px 93px;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  }
  .wrapper-teamInfo .teamImg .teamLogo {
    width: 154px;
    height: 144px;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    float: left;
  }
  .wrapper-teamInfo .teamImg .name {
    margin-left: 154px;
    width: 400px;
    padding-left: 33px;
    box-sizing: border-box;
  }
  .wrapper-teamInfo .teamImg .name .cname {
    font-size: 22px;
    font-weight: bold;
    color: #333333;
    margin-bottom: 13px;
    margin-top: 28px;
  }
  .wrapper-teamInfo .teamImg .name .enName {
    font-size: 20px;
    color: #B3B3B3;
  }
  .wrapper-teamInfo .teamInfo {
    width: 360px;
    height: 260px;
    float: left;
  }
  .wrapper-teamInfo .teamInfo .info-tit {
    font-weight: bold;
    font-size: 18px;
    color: #333333;
    border-bottom: 1px solid #D8D8D8;
    line-height: 50px;
  }
  .wrapper-teamInfo .teamInfo .info-desc {
    font-size: 14px;
    color: #676767;
    padding-top: 16px;
  }
  .wrapper-teamInfo .teamInfo .info-desc li {
    margin-bottom: 16px;
  }
}
