.schedule-cont .status {
  line-height: 22px;
  text-align: center;
}
.schedule-cont .team1,
.schedule-cont .team2 {
  line-height: 50px;
  padding-left: 16px;
  font-size: 12px;
  color: #333333;
}
.schedule-cont .team1 span,
.schedule-cont .team2 span {
  display: inline-block;
  vertical-align: middle;
}
.schedule-cont .img {
  width: 24px;
  height: 24px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.schedule-cont .name {
  margin-left: 4px;
  font-size: 12px;
  color: #333333;
}
.schedule-cont .score {
  float: right;
  margin-right: 16px;
  font-size: 14px;
  color: #333333;
  text-align: right;
  font-weight: bold;
}
