@media screen and (max-width: 1640px) {
  .recent-schedule {
    width: 760px;
    height: 230px;
    margin-bottom: 20px;
    overflow: hidden;
  }
  .recent-schedule .schedule-tit {
    border-bottom: 1px solid #D8D8D8;
    height: 45px;
    line-height: 45px;
  }
  .recent-schedule .schedule-tit .text {
    font-weight: bold;
    font-size: 18px;
    color: #333333;
    float: left;
  }
  .recent-schedule .schedule-tit .icon {
    float: right;
    cursor: pointer;
  }
  .recent-schedule .schedule-tit .icon span {
    display: inline-block;
    height: 45px;
    font-size: 12px;
    color: #676767;
  }
  .recent-schedule .schedule-tit .icon i {
    font-style: normal;
    display: inline-block;
    width: 30px;
    height: 24px;
    background: url(data:image/png;base64,) no-repeat;
    vertical-align: text-bottom;
  }
  .recent-schedule .scheduleList {
    margin-top: 10px;
    padding-left: 5px;
    height: 170px;
  }
  .recent-schedule .scheduleList .scheduleItem {
    float: left;
    width: 178px;
    height: 175px;
    background: url(data:image/png;base64,);
    background-size: 100%;
    background-repeat: no-repeat;
    margin-right: 10px;
  }
  .recent-schedule .scheduleList .scheduleItem .schedule-time {
    font-size: 12px;
    color: #FFFFFF;
    line-height: 40px;
    padding-left: 16px;
  }
  .recent-schedule .scheduleList .scheduleItem .schedule-time span:last-child {
    float: right;
    margin-right: 16px;
  }
  .recent-schedule .scheduleList .scheduleItem:last-child {
    margin-right: 0;
  }
}
